<template>
<div id="description" v-if="!italy&&!ireland&&!denmark&&!finland&&!sweden&&!portugal">
    <img class="img" id="explore1" alt="Explore Roadmap 1" src="../assets/Explore_1.png" >
    <img class="img" id="explore2" alt="Explore Roadmap 2" src="../assets/Explore_2.png" >
    <h3 id="descriptionText">Find the resources tailored for you</h3>
  </div>
  <div v-if="countryOpened" title="Back to Countries" v-on:click="backToCountries">
    <h2 class="backToCountries" >Countries <i class="fa-solid fa-sort-up sort-up"></i></h2>
  </div>
  <div v-if="!countryOpened" :id='countryOpened ? "countryOpened" : "countryClosed"'>
    <Country :italy="italy" country="Italy"  v-on:click="italyClicked"/>
    <Country :ireland="ireland" country="Ireland"  v-on:click="irelandClicked"/>
    <Country :denmark="denmark"  country="Denmark"  v-on:click="denmarkClicked"/>
    <Country :finland="finland" country="Finland"  v-on:click="finlandClicked"/>
    <Country :sweden="sweden" country="Sweden"  v-on:click="swedenClicked"/>
    <Country :portugal="portugal" country="Portugal" v-on:click="portugalClicked"/>
  </div>
    <CategoriesITA country="Italy" @close="closeCategory" v-if="italy"/>
    <CategoriesIRE country="Ireland" @close="closeCategory" v-if="ireland"/>
    <CategoriesDEN country="Denmark" @close="closeCategory" v-if="denmark"/>
    <CategoriesFIN country="Finland" @close="closeCategory" v-if="finland"/>
    <CategoriesSWE country="Sweden" @close="closeCategory" v-if="sweden"/>
    <CategoriesPOR country="Portugal" @close="closeCategory" v-if="portugal"/>
</template>

<script>
import Country from './Country.vue'
import CategoriesITA from './Categories/CategoriesITA.vue'
import CategoriesIRE from './Categories/CategoriesIRE.vue'
import CategoriesDEN from './Categories/CategoriesDEN.vue'
import CategoriesFIN from './Categories/CategoriesFIN.vue'
import CategoriesSWE from './Categories/CategoriesSWE.vue'
import CategoriesPOR from './Categories/CategoriesPOR.vue'

export default {
  name: 'Roadmap',
  components: {
      Country, 
      CategoriesITA,
      CategoriesIRE,
      CategoriesDEN,
      CategoriesFIN,
      CategoriesSWE,
      CategoriesPOR,
  },
  data() {
    return{
    italy:false,
    ireland:false,
    denmark:false,
    finland:false,
    sweden:false,
    portugal:false,
    countryOpened:false
    }
  },
  methods: {
    italyClicked(){this.ireland=false;this.denmark=false;this.finland=false;this.sweden=false;this.portugal=false;this.italy=!this.italy;this.countryOpened=!this.countryOpened},
    irelandClicked(){this.italy=false;this.denmark=false;this.finland=false;this.sweden=false;this.portugal=false;this.ireland=!this.ireland;this.countryOpened=!this.countryOpened},
    denmarkClicked(){this.italy=false;this.ireland=false;this.finland=false;this.sweden=false;this.portugal=false;this.denmark=!this.denmark;this.countryOpened=!this.countryOpened},
    finlandClicked(){this.italy=false;this.ireland=false;this.denmark=false;this.sweden=false;this.portugal=false;this.finland=!this.finland;this.countryOpened=!this.countryOpened},
    swedenClicked(){this.italy=false;this.ireland=false;this.denmark=false;this.finland=false;this.portugal=false;this.sweden=!this.sweden;this.countryOpened=!this.countryOpened},
    portugalClicked(){this.italy=false;this.ireland=false;this.denmark=false;this.finland=false;this.sweden=false;this.portugal=!this.portugal;this.countryOpened=!this.countryOpened},

    backToCountries(){
    console.log("pasta"); this.countryOpened=!this.countryOpened;
    this.ireland=false;this.denmark=false;this.finland=false;this.sweden=false;this.portugal=false;this.italy=false;
    },
    closeCategory(){
      this.italy=false;this.ireland=false;this.denmark=false;this.finland=false;this.sweden=false;this.portugal=false;this.countryOpened=!this.countryOpened;
    },
  }
}
</script>

<style scoped>
#countryClosed{
    margin:auto;
    align-items: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom:1rem;
}
#countryOpened{
    position:absolute;
    top: 0px;
    right: 0px;
}
.backToCountries{
    width:8rem;
    height:3rem;
    margin:0.8rem;
    font-size:0.9rem;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    display: flex;
    position:absolute;
    top:0.2rem;
    left:0.2rem;
    background: rgb(219,219,219);
    padding:0.1rem;
    border-bottom: 4px solid #2c3e50;
    box-sizing: border-box;  
}
.backToCountries:hover { 
  background: rgb(105, 204, 40);
  font-size:0.9rem;
  transform: scale(1.06);
  transition: 0.5s;
  }
.sort-up{
  font-size:1rem;
  margin-left:0.4rem;
  margin-top:0.5rem;
}
#description{
  margin-bottom:2rem;
  
}
#descriptionText{
  font-size: 1.4rem;
  padding-top:0.8rem;
  margin: 0 auto 0 auto;
 
}
.img{
  width:12rem;
}
#explore1{
  padding-right:0.6rem;
}
#explore2{
  padding-left:0.6rem;
}
</style>