<script>
import Resources from '../Resources.vue'

export default {
  name: 'CategoriesSWE',
    props: {
        country: String,
    },
   components: {
    Resources
  },
  emits: ["close"],
  data () {
    return {
      categories:true,
      areaA:false,
      areaB:false,
      areaC:false,
      areaD:false,
      areaE:false,
      areaF:false,
      areaG:false,

      subA:true,
      subB:true,
      subC:true,
      subD:true,
      subE:true,
      subF:true,
      subG:true,
      subH:true,
      subI:true,
      subL:true,

      backButton:false,
      back:true,

      resources: [],
      areas:[],
      resourceShow:false,

      component: '',
    }
  },
  mounted(){
    this.getAreas();
    },
  methods: {
     async getResource(area) {
      const response = await fetch('https://shelfie.labcd.unipi.it/wp-json/wp/v2/resource?language=17&areas='+area)
      const data = await response.json()
      this.resources = data;
      this.resourceShow=true;
    },
    async getAreas() {
      const response = await fetch('https://shelfie.labcd.unipi.it/wp-json/wp/v2/resource')
      const data = await response.json()
      this.areas = data;
    },
    async getSwe() {
      const response = await fetch('https://shelfie.labcd.unipi.it/wp-json/wp/v2/resource?language=17&&per_page=100')
      const data = await response.json()
      this.resources = data;
      this.resourceShow=true;
    },
    chooseSub(n){
      this.subA=false;this.subB=false;this.subC=false;this.subD=false;this.subE=false;this.subF=false;this.subG=false;this.subH=false;this.subI=false;this.subL=false;
      this.backButton=true;this.back=false;
      if(n==1){this.titleA=true};if(n==2){this.titleB=true};if(n==3){this.titleC=true};if(n==4){this.titleD=true};if(n==5){this.titleE=true};if(n==6){this.titleF=true};if(n==7){this.titleG=true};if(n==8){this.titleH=true};if(n==9){this.titleI=true};if(n==10){this.titleL=true};
    },
    showArea(n){
      this.subA=true;this.subB=true;this.subC=true;this.subD=true;this.subE=true;this.subF=true;this.subG=true;this.subH=true;this.subI=true;this.subL=true;
      this.categories=false;
      if(n==1){this.areaA=true};if(n==2){this.areaB=true};if(n==3){this.areaC=true};if(n==4){this.areaD=true};if(n==5){this.areaE=true};if(n==6){this.areaF=true};if(n==7){this.areaG=true}; 
    },
    goBack(){
      this.areaA=false;this.areaB=false;this.areaC=false;this.areaD=false;this.areaE=false;this.areaF=false;this.areaG=false;
      this.categories=true;this.backButton=false;this.resourceShow=false;
      this.titleA=false;this.titleB=false;this.titleC=false;this.titleD=false;this.titleE=false;this.titleF=false;this.titleG=false;this.titleH=false;this.titleI=false;this.titleL=false
    },
    backToArea(n){
      this.subA=true;this.subB=true;this.subC=true;this.subD=true;this.subE=true;this.subF=true;this.subG=true;this.subH=true;this.subI=true;this.subL=true;
      this.backButton=false;this.back=true;this.resourceShow=false;
      if(n==1){this.areaA=true;};if(n==2){this.areaB=true;};if(n==3){this.areaC=true;};if(n==4){this.areaD=true;};if(n==5){this.areaE=true;};if(n==6){this.areaF=true;};if(n==7){this.areaG=true;};
      this.titleA=false;this.titleB=false;this.titleC=false;this.titleD=false;this.titleE=false;this.titleF=false;this.titleG=false;this.titleH=false;this.titleI=false;this.titleL=false
    },
    close(){
      this.areaA=false;this.areaB=false;this.areaC=false;this.areaD=false;this.areaE=false;this.areaF=false;this.areaG=false;
      this.categories=false;
      this.backButton=false;
      this.$emit("close");
    }
  }
}
</script>

